html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--Regular);
  font-weight: 400;
  font-size: 16px;
  line-height: 1.3;
  background-color: var(--main-bg);
  color: var(--main-color);
}

h1,
h2,
h3,
p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  outline: none;
  text-decoration: none;
  cursor: pointer;
}

button {
  margin: 0;
  padding: 0;
  background-color: transparent;
  color: inherit;
  border: 2px solid transparent;
  outline: none;
  cursor: pointer;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

svg {
  height: 100%;
}

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
}

input {
  color: inherit;
  background-color: transparent;
  border: 2px solid transparent;
  outline: none;
  -webkit-appearance: none;
  appearance: none;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration,
  &::-webkit-inner-spin-button {
    display: none;
  }
}

table {
  border-spacing: 0px;
  border-collapse: collapse;
}

tr,
th,
td {
  margin: 0;
  padding: 0;
}

.lazyload,
.lazyloading {
  opacity: 0;
}
.lazyloaded {
  opacity: 1;
  transition: opacity 300ms;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  --containerWidth: 1124px;
  --containerOffset: 36px;
  max-width: calc(var(--containerWidth) + (var(--containerOffset) * 2));
  padding: 0 var(--containerOffset);
  margin: 0 auto;
}

.yellow {
  color: var(--yellow-color);
}

.green {
  color: var(--green-color);
}

.orange {
  color: var(--orange-color);
}

.red {
  color: var(--red-color);
}

@include laptop {
  .container {
    --containerOffset: 25px;
  }
}

@include tablet {
  .container {
    --containerOffset: 30px;
  }
}

@include small-tablet {
  .container {
    --containerOffset: 10px;
  }
}

@include mobile {
  .container {
    --containerOffset: 5px;
  }
}
